// 
import Vue from 'vue';
import Router from 'vue-router';

import index from '@/views/index';
import Model from '@/views/system/model'
import Login from '@/views/login'

/**
 * 
 */
// 
import user from '@/views/system/user';


// 
Vue.use(Router);

// 
export default new Router({
  routes: [{
    path: '/',
    component: Model,
  }, {
    path: '/login',
    name: '',
    component: Login,
    meta: {
      requireAuth: true
    },
  }, {
    path: '/index',
    name: '',
    component: index,
    iconCls: 'el-icon-tickets',
    children: [{
      path: '/system/user',
      name: '',
      component: user,
      meta: {
        requireAuth: true
      }
    }, {
      path: '/system/model',
      name: '',
      component: Model,
      meta: {
        requireAuth: true
      }
    }]
  }]
})



