<template>
  <el-container class="main-con">
    <el-header class="index-header">
      <navcon></navcon>
    </el-header>
    <el-main class="index-main">
      <div>
        <el-form :inline="true" class="dept-search">
          <el-form-item>
            <el-input
              v-model="query.name"
              placeholder=""
              style="width:150px"
            />
          </el-form-item>
          <el-form-item>
            <el-input
              v-model="query.code"
              placeholder=""
              style="width:150px"
            />
          </el-form-item>
          <el-form-item>
            <el-button size="small" type="primary" icon="el-icon-search" @click="getList()"></el-button>
            <el-button size="small" type="primary" icon="el-icon-plus" @click="handleCreate()"></el-button>
          </el-form-item>
        </el-form>
        <!--    -->
        <div class="dept-show">
          <el-row :gutter="20">
            <el-col :span="6" v-for="item in deptData" :key="item.enterprisename" style="margin-top: 20px">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span>{{ item.enterprisename }}</span>
                  <el-button style="float: right; padding: 3px 0" type="text" @click="openDialog()"></el-button>
                </div>
                <div class="card-context">
                  <el-descriptions column="1">
                    <el-descriptions-item label="">
                      <el-tag type="info">
                        {{ item.enterprisecode }}
                      </el-tag>
                    </el-descriptions-item>
                    <el-descriptions-item label="">
                      <el-tag>{{ item.enterprisedetail }}</el-tag>
                    </el-descriptions-item>
                  </el-descriptions>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </div>

        <!--    --->
        <el-dialog title="" :visible.sync="editFormVisible" width="70%" @click="closeDialog">
          <el-card class="box-card1">
            <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
              <el-tab-pane label="" name="first">
                <el-table :data="farmData" size="small" highlight-current-row border style="width: 100%;">
                  <el-table-column align="center" sortable prop="farmName" label="" width="200px">
                  </el-table-column>
                  <el-table-column align="center" sortable prop="farmCode" label="" width="200px">
                  </el-table-column>
                  <el-table-column align="center" sortable prop="farmDetail" label="" width="200px">
                  </el-table-column>
                  <el-table-column align="center" label="" min-width="200px">
                    <template slot-scope="scope">
                      <el-button size="mini" @click="farmEdit(scope.$index, scope.row)"></el-button>
                      <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-tab-pane>
              <el-tab-pane label="" name="second">
                <el-table size="small" highlight-current-row border style="width: 100%;">
                  <el-table-column align="center" sortable prop="deptName" label="" width="200px">
                  </el-table-column>
                  <el-table-column align="center" sortable prop="userName" label="" width="200px">
                  </el-table-column>
                  <el-table-column align="center" sortable prop="userRealName" label="" width="200px">
                  </el-table-column>
                  <el-table-column align="center" label="" min-width="200px">
                    <template slot-scope="scope">
                      <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
                      <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-tab-pane>
              <el-tab-pane label="" name="third" >
                <Tablelist></Tablelist>
              </el-tab-pane>
            </el-tabs>
          </el-card>
        </el-dialog>

        <!--        --->
        <el-dialog :visible.sync="dialogFormVisible">
          <el-form :model="form">
            <el-form-item label="">
              <el-input v-model="form.enterprisename" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.enterprisecode" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.enterprisedetail" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="submitForm()"> </el-button>
          </div>
        </el-dialog>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import Navcon from "@/components/navcon";
import Tablelist from "@/views/system/tablelist";
export default {
  name: "model",
  components: {Navcon,Tablelist},
  data() {
    return {
      activeName: 'first',
      editFormVisible: false,
      dialogFormVisible: false,
      //
      deptData: [
      //   {
      //   id: 1,
      //   enterprisename: '',
      //   enterprisecode: 'WHHS000001',
      //   enterprisedetail: ''
      // },{
      //   id: 3,
      //   enterprisename: '',
      //   enterprisecode: 'WHHS000002',
      //   enterprisedetail: ''
      // },{
      //   id: 4,
      //   enterprisename: '',
      //   enterprisecode: 'WHHS000003',
      //   enterprisedetail: ''
      // },{
      //   id: 5,
      //   enterprisename: '',
      //   enterprisecode: 'WHHS000004',
      //   enterprisedetail: ''
      // },{
      //   id: 1231232313,
      //   enterprisename: '',
      //   enterprisecode: 'WHHS000007',
      //   enterprisedetail: ''
      // },{
      //   id: 12312311233,
      //   enterprisename: '',
      //   enterprisecode: 'WHJX000001',
      //   enterprisedetail: ''
      // },{
      //   id: 123123897733,
      //   enterprisename: '',
      //   enterprisecode: 'WHJX000002',
      //   enterprisedetail: ''
      // },{
      //   id: 69612237697978368,
      //   enterprisename: '',
      //   enterprisecode: 'WHHS000005',
      //   enterprisedetail: ''
      // },{
      //   id: 69612655568097280,
      //   enterprisename: '',
      //   enterprisecode: 'WHHS000006',
      //   enterprisedetail: ''
      // },{
      //   id: 74298751908450304,
      //   enterprisename: '',
      //   enterprisecode: 'WHJX000008',
      //   enterprisedetail: ''
      // }
      ],
      total: 0,
      query: {
        page: 1,
        size: 15,
        name: '',
        code: ''
      },
      form: {
        id: '',
        enterprisename: '',
        enterprisecode: '',
        enterprisedetail: ''
      },
      farmData: [
        {
          farmName: 'A',
          farmCode: '00001',
          farmDetail: '',
        },
        {
          farmName: 'B',
          farmCode: '00002',
          farmDetail: '',
        },
        {
          farmName: 'C',
          farmCode: '00003',
          farmDetail: '',
        },
        {
          farmName: '',
          farmCode: '00004',
          farmDetail: '',
        },
        {
          farmName: 'A',
          farmCode: '00005',
          farmDetail: '',
        },
      ],
    }
  },
  mounted() {
    // this.getTreeData();
    this.getList();
  },
  methods: {
    getList() {
      this.axios.get('http://111.4.83.67:53313/modelData/getList', {
        params: {
          page: this.query.page,
          size: this.query.size,
          enterprisename: this.query.name,
          enterprisecode: this.query.code,
        }
      }).then((resp) => {
        console.log(resp);
        this.deptData = resp.data.content.list;
        this.total = resp.data.content.total;
      })
    },
    handleCreate() {
      this.dialogFormVisible = true
    },
    openDialog() {
      this.editFormVisible = true
    },
    closeDialog() {
      this.editFormVisible = false
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    submitForm() {
      console.log(this.form),
      this.axios.post('http://111.4.83.67:53313/modelData/getList', this.form).then((resp) => {
        let data = resp.data;
        if (data.success) {
          this.dialogFormVisible = false;
          this.form = {};
          this.getList();
          this.$message({
            message: '',
            type: 'success'
          });
        }
      })
    },
  }

}
</script>

<style scoped>
.dept-search {
  margin-top: 20px;
}

.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}

.box-card {
  height: 200px;
}

.index-header,
.index-main {
  padding: 0px;
  border-left: 2px solid #333;
}

.main-con {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}

.detail-dialog {
  display: flex;
}
</style>



